<template>
  
  <div class="content-overflow"
  
  >
  <!-- style="max-height: calc(100vh - 350px);" -->

  <div class="content-wrapper"
 
  >
    <!-- <div ref="forqr">1111111111111111111111111</div> -->
  
    <div class="content-body"
        v-for="(item,index) in actualView.bodyContent"
        :key="index"
    >
        <InfoTitle
          v-if="item.contentTitle"
          :contentTitle="item.contentTitle"
        >            
        </InfoTitle>

        <InfoText
          v-if="item.contentText"
          :contentText="item.contentText"
        >            
        </InfoText>

        <InfoQR
          v-if="item.contentQR"
          :contentQR="item.contentQR"
        >            
        </InfoQR>
        
        <ChartQR
          v-if="item.contentChart"
        >            
        </ChartQR>        

        <InfoImage
          v-if="item.contentImage"
          :contentImage="item.contentImage"
        >
        </InfoImage>

        <InfoPercent
          v-if="item.contentPercent"
        >
        </InfoPercent>
        
    </div>

    <!-- <AcceptCheckbox
      v-if="actualView.acceptCheckbox"  
      :isAccept=isAccept
      @changeAccept = changeAccept
    /> -->

  <!-- </div> -->

  </div>
  
    <UserFeedback
        v-if="actualView.feedback"
      />

  </div>
  </template>
  
 <script>
import { mapActions } from 'vuex'
import { mapGetters } from 'vuex'
import InfoTitle from '@/components/InfoTitle.vue'
import InfoText from '@/components/InfoText.vue'
import InfoQR from '@/components/InfoQR.vue'
import ChartQR from '@/components/ChartQR.vue'
import InfoImage from '@/components/InfoImage.vue'
import UserFeedback from '@/components/UserFeedback.vue'
import InfoPercent from "@/components/InfoPercent.vue"

export default {
    name: "InfoContent",
    data() {
      return {
        isAccept: false,
        questionsObject: {
        question: "Как дела?",
        answers: [
          {
            id: 1,
            name: "Норм",
          },
          {
            id: 2,
            name: "Не норм",
          },
        ],
        type: 1,
      },

      }
    },
    components: {
        InfoTitle,
        InfoText,
        InfoImage,
        UserFeedback,
        InfoPercent,
        InfoQR,
        ChartQR
    },
    computed: {
      ...mapGetters([
          "gameData",
          "stepNumber",
          "actualView",
          "actualProperty"
      ])
      // classBodyContent(){
      //   if (this.actualView.acceptCheckbox) {
      //     return "px-28 py-6 border-b border-slate-700 flex flex-col"
      //   }
      //   else {
      //     return "px-12 py-6 flex-col flex items-center overflow-y-auto"
      //   }
      // }        
    },
    methods: {
      ...mapActions([
          "setAcceptCheckbox"
      ]),

      changeAccept() {
        this.isAccept = !this.isAccept
        this.setAcceptCheckbox(this.isAccept)
      },

      async sendVoteQuestions(questionsObject) {
        try {
          const rawResponse = await fetch(
            "https://pitech.me/medicine/api/v1/voting/qrcode",
            {
              method: "POST",
              // mode: "no-cors",
              headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
              },
              body: JSON.stringify(questionsObject),
            }
          );
          return await rawResponse.json();
        } catch (e) {
          return { error: 1 };
        }
      },

      async getQR(questionsObj) {
        const { qrcode, session, error } = await this.sendVoteQuestions(questionsObj);
        console.log(session);
        
        const bodyBlock = this.$refs.forqr
        bodyBlock.innerHTML = "";

        if (error) {
          bodyBlock.innerHTML =
            "<h2>Нет связи с сервером :( </h2><div>Нажмите далее для продолжения<div>";
        } else {
          var image = new Image();
          image.src = qrcode;
          bodyBlock.appendChild(image);
        }
      }

    },    

    mounted() {
      // this.getQR(this.questionsObject)
    }
}
</script>

